import { Movie } from "../Components/movie.model";
import { Injectable } from "@angular/core";
import {
  HttpClient,
  HttpHeaders,
  HttpParams
} from "@angular/common/http";
// import { timingSafeEqual } from 'crypto';

/*
* A service Class that acts as centralized repository for handling movie data.
* @Author: Sai Saran Kandimalla.
* @Author: Shilpa Bhat
* @version 1: created on 09/18/2018.
*/
@Injectable()
export class MovieService {

  //Sample data for movies.
  public movies: Movie[] = [];
  private movie_id: number;

  constructor(private http: HttpClient) { }

  /* A getter method to get movies.
    * @returns movies: Movie[].
    */
  getMovies(id: number) {
    return this.http.get("/movies/?page=" + id, { responseType: "json" });
  }

  /* A getter method to get movie details */
  getMovieDetails(id: number) {
    this.movie_id = id;
    return this.http.get("/movies/" + id, {
      responseType: "json"
    });
  }

  getSearchedMovieList(movieName: string, details: string) {

    return this.http.get(
      "/movies/?type=search&name=" + movieName + "&details=" + details
    );
  }

  getUpcomingMovieList(id: number) {
    return this.http.get("/movies/?type=upcoming&page=" + id, {
      responseType: "json"
    });
  }

  getSimilarMovies(id: number) {
    return this.http.get(
      "/movies/" + id + "/?type=similar",
      {
        responseType: "json"
      }
    );
  }

  getShowTimes(days: number) {
    return this.http.get("/showtimes/" + this.movie_id + "?date=" + days, {
      responseType: "json"
    });
  }
}
